.footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    padding: 0rem 0rem;
    width: 100vw;
    margin-top: 0px;
    margin-bottom: 0px;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 3;
}

.footer p{
    text-align: center;
    align-content: center;
}


.footerIcons{
    display: flex;
    justify-content: center;
    gap: 1.5rem;
}

img.socialIcon {
    height: 30px;
    width: 30px;
    padding:30px;  
}
  
img.socialIcon:hover{
    background-color: rgb(171, 172, 172);
    border-radius: 25px;
    color:rgb(255, 255, 255);
}
  
img.socialIcon:active{
    background-color: rgb(76, 78, 78);
    border-radius: 25px;
    color:rgb(255, 255, 255);
}
  