
.navbar{
	background-color: rgb(122, 125, 131);
	display: inline-block;
	padding:20px;
	width: 100%;
	text-align:center;
	margin-top: 0px;
	margin-bottom: 0px;
	position: fixed;
	z-index: 3;
}

.navbar li{
	list-style: none;
	display:inline-block;
	margin:auto;
}

.navbar li a{
	text-decoration: none;
	color:rgb(0, 0, 0);
	font-size: 20px;
	padding:20px;
}

.navbar li a:hover{
	background-color: rgb(171, 172, 172);
	border-radius: 5px;
	color:rgb(255, 255, 255);
}

.navbar li a:active{
	background-color: rgb(76, 78, 78);
	border-radius: 5px;
}
